*,
*::before,
*::after {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
		"Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.seriesList {
	list-style-type: none;
	padding: 0;
	max-height: 450px;
	overflow: auto;
}

.searchResult {
	display: flex;
	margin-top: 10px;
	color: black;
	cursor: pointer;
}

.searchResult .resultItem {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;
	text-align: left;
	margin-left: 5px;
}

.mono {
	color: #8e95a5;
	font-family: Roboto, sans-serif;
	font-size: .8rem;
	text-decoration: none;
}

.prevEpisode p {
	margin: 0;
}

.noMargin {
	margin: 0;
}

.seriesContainer,
.personContainer,
.castWrapper {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	justify-content: center;
	/* align-items: space-around; */
	max-width: 1200px;
	padding: 0 20px;
	margin: 0 auto;
}

.seriesContainer a,
.personContainer a,
.episodesWrapper a,
.castWrapper a {
	text-decoration: none;
	color: #4281a4;
}

.seriesContainer h1,
.personContainer h1,
.episodesWrapper h1,
.castWrapper h1 {
	font-size: 2.8rem;
	font-weight: 300;
	align-self: flex-start;
	margin-top: 40px;
}

.seriesInfo,
.personInfo {
	display: flex;
	margin: 20px auto;
	line-height: 1.5rem;
}

.personInfo {
	display: flex;
	justify-content: flex-start;
}

.personInfo .wiki {
	box-shadow: none;
	margin-left: 10px;
}

.seriesInfo img,
.personInfo img {
	flex: none;
	height: 290px;
}

.seriesSummary {
	max-width: 50vw;
	text-align: justify;
	margin: 0 15px;
}

.officialLink {
	margin-top: 15px;
}

.currentEpisodes {
	display: flex;
	flex: none;
	justify-content: space-around;
	min-width: 200px;
}

.currentEpisodeSummary {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	background-color: #fbfcff;
	box-shadow: 1px 1px 2px #4281a4;
	padding: 0 20px;
	min-width: 300px;
	margin-right: 1px;
}

.recentEpisodes h3,
.impCasts h3,
.currentEpisodeSummary h3,
.castCredit h3,
.episodesWrapper h2,
.castWrapper h2 {
	font-size: 2rem;
	font-weight: 300;
	margin-bottom: 10px;
	text-align: left;
}

.recentEpisodes,
.impCasts,
.castCredit {
	margin-top: 40px;
	align-self: stretch;
}

.epInfo {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.epInfo span span {
	margin-left: 4px;
}

.seeAll {
	display: flex;
	justify-content: flex-end;
	margin-top: 10px;
}

.seeAll span {
	border: 1px solid #4281a4;
	padding: 5px;
	border-radius: 8px;
}

.seeAll span:hover {
	background-color: #4281a4;
	color: #fff;
}

.episodesWrapper,
.castWrapper {
	max-width: 1200px;
	margin: 0 auto;
	padding: 0 20px;
}

.seasonTitle {
	display: flex;
	justify-content: space-between;
	align-items: center;
	color: #fff;
	margin-top: 20px;
	background-color: #4281a4;
	padding: 0px 20px;
}

.seasonTitle span {
	margin-right: 20px;
	font-size: 1.5rem;
}
td.episodeTitle {
	width: 70%;
}

h1.seriesName {
	font-size: 2.8rem;
	font-weight: 300;
	align-self: flex-start;
	margin-top: 40px;
	text-align: left;
	margin-bottom: 20px;
}

@media (min-width: 320px) and (max-width: 480px) {
	.seriesContainer,
	.personConteainer,
	.episodesWrapper,
	.castWrapper {
		max-width: 480px;
		display: flex;
	}

	.episodesWrapper,
	.castWrapper {
		flex-direction: column;
		padding: 0 10px;
	}

	.seriesContainer h1,
	.personContainer h1,
	.episodesWrapper h1,
	.castWrapper h1 {
		margin-top: 15px;
		font-size: 2.4rem;
	}

	.seriesInfo,
	.personInfo {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		max-width: 400px;
	}

	.seriesSummary {
		max-width: 100%;
	}

	.seriesInfo img,
	.personInfo img {
		width: 100%;
		height: auto;
		margin-bottom: 10px;
	}

	.currentEpisodes {
		flex-wrap: wrap;
	}

	.currentEpisodeSummary {
		margin-top: 10px;
		width: 100%;
	}
}

@media (min-width: 670px) and (max-width: 1024px) {
	.impCasts .card:last-child {
		display: none;
	}
}

.header {
	background-color: #34d1bf;
	font-size: calc(10px + 2vmin);
	color: white;
}
.headerWrapper {
	display: flex;
	justify-content: space-around;
	margin-top: 10px;
}

.logo a {
	display: flex;
	align-items: center;
	justify-content: center;
}

.logo a p {
	font-family: 'Monoton', cursive;
	margin-right: 20px;
	font-size: 2rem;
}

.header a {
	text-decoration: none;
	color: white;
}

.activeNow {
	border-bottom: 3px solid #fff;
}

.navItem {
	margin-right: 20px;
	padding: 10px auto;
}

@media (min-width: 320px) and (max-width: 480px) {
	.header nav a {
		font-size: 1.5rem;
	}

	.search {
		width: 98%;
	}
}

.wrapper {
	display: flex;
	flex-direction: column;
	box-shadow: 0px -5px 10px rgba(0, 0, 0, .3);
}

.center {
	margin: 30px auto;
}

a button {
	margin-top: 20px;
	width: 200px;
	padding: 0 20px;
	text-align: center;
	font-size: 1.8rem;
	background-color: #fff;
	border-color: #34d1bf;
	border-radius: 5px;
	cursor: pointer;
}

.extraInfo {
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
	align-content: space-between;
	align-items: center;
}

.left {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.left h4 {
	align-self: center;
}

.left svg {
	margin-right: 10px;
}

.contacts {
	display: flex;
	justify-content: center;
}

.srOnly {
	position: absolute;
	left: -10000px;
	width: 1px;
	height: 1px;
	top: auto;
	overflow: hidden;
	white-space: nowrap;
}

.right h4 {
	text-align: center;
}

@media (min-width: 320px) and (max-width: 480px) {
	.right {
		margin-top: 20px;
	}
}

/* Generic stuffs 
-----------------------------------*/
html {
	height: 100%;
}

body,
#root {
	overflow: auto;
}

.App {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	text-align: center;
}

.appWrapper {
	margin-right: auto;
	margin-left: auto;
	max-width: 960px;
	padding-right: 10px;
	padding-left: 10px;
}

.center {
	text-align: center;
}

.textInWhite {
	color: white;
}

.highlight {
	background: #34d1bf;
	color: #fff;
	padding: 0px 5px;
	border-radius: 3px;
}

table {
	border-collapse: collapse;
	width: 100%;
}

th,
td {
	text-align: left;
	padding: 8px;
}

tbody tr:nth-child(odd) {
	background-color: #f2f2f2;
}

tbody tr:hover {
	background-color: #d0ccd0;
}

.cards {
	display: flex;
	justify-content: space-evenly;
	min-width: 200px;
	flex-wrap: wrap;
}

.card {
	display: flex;
	flex-direction: column;
	box-shadow: 2px 0px 4px #4281a4;
	margin-top: 10px;
}

.card:hover {
	box-shadow: 4px 2px 4px #4281a4;
}

.cardTitle {
	text-align: left;
	background-color: rgba(66, 129, 164, .1);
	color: white;
	padding: 5px 10px;
	width: 100%;
}

.cardDescription {
	text-align: left;
	font-size: 12px;
	text-align: center;
}

.titleCase {
	text-transform: capitalize;
}

/* Header stuffs
----------------------------------- */

.header {
	flex: none;
}

/* Footer Stuffs
----------------------------- */
.footer {
	background: #282c34;
	color: white;
	flex: none;
	margin-top: 40px;
}

/* Main stuffs
-------------------- */

.main {
	flex: 1 0 auto;
}

.search {
	width: 100%;
	max-width: 800px;
	margin: 0 auto;
}

.search input {
	width: 100%;
	line-height: 50px;
}

.seriesList li:nth-of-type(odd) {
	background-color: #e5f2f4;
}

.description {
	background: -webkit-gradient(linear, left top, left bottom, from(rgba(66, 129, 164, 0.9)), to(rgba(66, 129, 164, 0.9))), url(/ShowTime/static/media/cover.4529d685.png) center;
	background: -webkit-linear-gradient(rgba(66, 129, 164, 0.9), rgba(66, 129, 164, 0.9)), url(/ShowTime/static/media/cover.4529d685.png) center;
	background: linear-gradient(rgba(66, 129, 164, 0.9), rgba(66, 129, 164, 0.9)), url(/ShowTime/static/media/cover.4529d685.png) center;
	padding: 10px;
	min-height: 30vh;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.inputSection {
	margin-top: 25px;
	width: 98%;
}

.wiki {
	display: flex;
	flex-direction: column;
	flex: none;
	text-align: left;
	font-size: .95rem;
	background-color: #fbfcff;
	padding: 10px 20px;
	box-shadow: 5px 5px 10px #4281a4;
	line-height: 1.7rem;
}

.wiki h2 {
	font-weight: 500;
	margin-bottom: 10px;
}

/* Mobile Phones */
@media (min-width: 320px) and (max-width: 480px) {
	.wiki {
		align-self: stretch;
		margin-top: 20px;
		box-shadow: 1px 1px 2px #4281a4;
	}

	.card {
		margin-bottom: 10px;
	}

	.search {
		max-width: 96%;
	}
}

/* Mobile Phones in Landscape, Tablets */
@media (min-width: 481px) and (max-width: 1024px) {
	.seriesInfo {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-evenly;
	}

	.seriesSummary {
		order: 2;
		max-width: 100%;
		margin-top: 20px;
	}

	.wiki {
		box-shadow: none;
	}
}

